#root {
  height: 100vh;
  min-width: 320px;
}

.cursor-pointer {
  cursor: pointer;
}

@media (max-width: 375px) {
  .hidden-xs {
    display: none;
  }
}

@media (max-width: 425px) {
  .hidden-sm {
    display: none;
  }
}

@media (max-width: 720px) {
  .hidden-md {
    display: none;
  }
}

@media (max-width: 980px) {
  .hidden-lg {
    display: none;
  }
}
